import DashboardLayout from "../../(dashboard)/dashboard-layout";

export default function PatientDashboard() {
  return (
    <DashboardLayout>
      <h1 className="text-3xl font-bold mb-6">Patient Dashboard</h1>

      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        
        {/* Card 1 */}
        <div className="bg-white shadow rounded-xl p-6">
          <h2 className="text-xl font-semibold mb-2">Mes Rendez-vous</h2>
          <p className="text-gray-600">Voir vos consultations programmées.</p>
        </div>

        {/* Card 2 */}
        <div className="bg-white shadow rounded-xl p-6">
          <h2 className="text-xl font-semibold mb-2">Dossier Médical</h2>
          <p className="text-gray-600">Historique des visites et analyses.</p>
        </div>

        {/* Card 3 */}
        <div className="bg-white shadow rounded-xl p-6">
          <h2 className="text-xl font-semibold mb-2">Messages</h2>
          <p className="text-gray-600">Communiquer avec votre docteur.</p>
        </div>
      </div>
    </DashboardLayout>
  );
}
